@charset "utf-8";
.flex {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}
.l_flex {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  -ms-flex: 1;
  flex: 1;
}
body {
  font-size: 14px;
  color: #666;
  background: #fff;
}
html,
body,
div,
table,
ul,
li,
ol,
dl,
dd,
dt,
a,
img,
label,
p,
h1,
h2,
h3,
h4,
h5,
h6,
span {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
}
input {
  margin: 0;
  padding: 0;
}
ul,
ol {
  list-style: none;
}
em,
i {
  font-style: normal;
}
strong {
  font-weight: normal;
}
img {
  max-width: 100%;
  display: block;
}
textarea {
  resize: none;
}
form {
  margin: 0;
}
button {
  outline: none!important;
}
button:focus {
  outline: none!important;
}
a {
  color: #4f5f6f;
}
a,
a:hover,
a:focus {
  text-decoration: none;
  outline: none;
}
a:hover,
a:focus {
  color: #2286d3;
}
.wrap {
  width: 100%;
  margin: 0 auto;
}
.blue {
  color: #2286d3;
}
.red {
  color: red;
}
.ori {
  color: #fcb537;
}
